import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./ContactPageContent.module.scss"

export default () => {
  const { pine, iconPhone, iconMail, iconSkype } = useStaticQuery(graphql`
    query {
      pine: file(relativePath: { eq: "pine.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconPhone: file(relativePath: { eq: "iconPhone.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconMail: file(relativePath: { eq: "iconMail.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconSkype: file(relativePath: { eq: "iconSkype.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <div className={styles.mainContent}>
      <div className={styles.headingContainer}>
        <Img
          fixed={pine.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
        />
        <div className={styles.heading}>
          Let's Make Something Great Together
        </div>
        <Img
          fixed={pine.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
          imgStyle={{
            WebkitTransform: "scaleX(-1)",
            transform: "scaleX(-1)",
          }}
        />
      </div>
      <div className={styles.contactInfoSection}>
        <div>
          <div className={styles.contactInfoRow}>
            <Img
              fixed={iconPhone.childImageSharp.fixed}
              objectFit="cover"
              objectPosition="50% 50%"
              alt=""
            />
            <span className={styles.contactInfoText}>(+84) 962 61 81 22</span>
          </div>
          <div className={styles.contactInfoRow}>
            <Img
              fixed={iconMail.childImageSharp.fixed}
              objectFit="cover"
              objectPosition="50% 50%"
              alt=""
            />
            <span className={styles.contactInfoText}>ntbichha95@gmail.com</span>
          </div>
          <div className={styles.contactInfoRow}>
            <Img
              fixed={iconSkype.childImageSharp.fixed}
              objectFit="cover"
              objectPosition="50% 50%"
              alt=""
            />
            <span className={styles.contactInfoText}>hanguyen459</span>
          </div>
        </div>
      </div>
    </div>
  )
}
